<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <!-- 商品底价 -->
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="公司名称" size="mini">
            <erpCompanyList v-model='params.company_id' :fatherParams='params' />
          </el-form-item>
          <el-form-item label="策略名称" size="mini">
            <el-input v-model="params.price_strategy_name" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="品牌" size="mini">
            <getBrand v-model='params.brand_id' @change="onSearch(1)" />
          </el-form-item>
          <el-form-item label="策略有效年月" size="mini">
            <el-date-picker v-model="policyMonth" type="monthrange" value-format='yyyy-MM' range-separator="至" start-placeholder="开始月份" end-placeholder="结束月份"></el-date-picker>
          </el-form-item>
          <el-form-item label="状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择" @change="onSearch(1)">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="创建日期" size="mini">
            <el-date-picker v-model="createdDay" type="daterange" @change="onSearch(1)" value-format='yyyy-MM-dd' range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="创建人" size="mini">
            <el-input v-model="params.add_user_name" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="examineDay" type="daterange" @change="onSearch(1)" value-format='yyyy-MM-dd' range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"> </el-date-picker>
          </el-form-item>
          <el-form-item label="审核人" size="mini">
            <el-input v-model="params.auditor_name" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="goAdd({})" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="examination('删除')" size="mini" v-if="authority.b_del == 1">删除</el-button>
        <el-button type="primary" icon="iconfont icon-pass" @click="examination('审核')" size="mini" v-if="authority.b_pass == 1">审核</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="examination('弃审')" class="addBtn" size="mini" v-if="authority.b_nopass == 1">弃审</el-button>
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export == 1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div>
        <xTable :ajax='{ url: "getPriceStrategyList", params: params }' ref='xTable' :tableColumn='tableColumn' />
      </div>
    </div>
  </div>
</template>

<script>
import { paramsModule, tableModule } from "./config/shoplowPriceList";
export default {
  mixins: [tableModule],
  name: "shoplowPriceSearch",
  data() {
    return {
      policyMonth: [], // 策略年月
      createdDay: [], // 创建日期
      examineDay: [], // 审核日期
      params: this.$publicFution.deepCopy(paramsModule)
    };
  },
  methods: {
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.policyMonth = []; // 策略年月
      this.createdDay = []; // 创建日期
      this.examineDay = []; // 审核日期
      this.onSearch(1);
    },
    examination(type) {
      if (this.multipleSelection.length === 0) {
        this.$message.warning("请勾选单据！");
        return;
      }
      var box = this.multipleSelection.map(val => val.price_strategy_id);
      var api;
      if (type === "审核") {
        api = "auditorPriceStrategy";
      } else if (type === "弃审") {
        api = "unAuditorPriceStrategy";
      } else if (type === "删除") {
        api = "delPriceStrategy";
      }
      this.$confirm("是否继续?", "温馨提示！", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.$axios({
            method: "post",
            data: { price_strategy_id: box.join(",") },
            url: this.$api[api]
          }).then(({ data }) => {
            if (data.code === 200) {
              this.onSearch();
              this.$message.success(data.msg);
            }
          });
        })
        .catch(() => {});
    },
    // 导出
    exportOrder(scheduleType) {
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axios({
        method: "get",
        params: s,
        url: this.$api.exportPriceStrategy
      }).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    getTime(arr) {
      if (Array.isArray(arr)) {
        return arr;
      }
      return [];
    },
    onSearch(number) {
      this.params.effective_start_time = this.getTime(this.policyMonth)[0]
        ? this.getTime(this.policyMonth)[0] + "-01"
        : "";
      this.params.effective_end_time = this.getTime(this.policyMonth)[1]
        ? this.getTime(this.policyMonth)[1] + "-01"
        : "";
      this.params.add_start_time = this.getTime(this.createdDay)[0];
      this.params.add_end_time = this.getTime(this.createdDay)[1];
      this.params.auditor_start_time = this.getTime(this.examineDay)[0];
      this.params.auditor_end_time = this.getTime(this.examineDay)[1];
      this.$refs.xTable._getData(number);
    },
    goDetail(row) {
      this.$router.push({
        path: "/purchase/shoplowPrice/shoplowPriceSee",
        query: {
          id: row.price_strategy_id
        }
      });
    },
    goAdd(row) {
      this.$router.push({
        path: "/purchase/shoplowPrice/shoplowPriceAdd",
        query: {
          id: row.price_strategy_id
        }
      });
    }
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>

