export const tableModule = {
  data() {
    return {
      footerData: {},
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center',
          fixed: 'left'
        },
        {
          type: 'index',
          title: '序号',
          width: 50
        },
        {
          field: 'name',
          title: '操作',
          width: 100,
          fixed: "right",
          slots: {
            default: (scope) => {
              return [
                <el-button size="small" type="text" disabled={this.authority.b_info !== 1} onClick={() => this.goDetail(scope.row)} > 查看 </el-button>,
                <el-button size="small" type="text" disabled={!(scope.row.auditor_status === 1 && this.authority.b_edit === 1)} onClick={() => this.goAdd(scope.row)}  > 修改  </el-button>,
              ]
            }
          }
        },
        {
          title: '单据号',
          field: 'price_strategy_sn',
          width: 150,
          slots: {
            default: ({ row }) => {
              return [
                <a href="javaScript:void()" onClick={() => this.goDetail(row)}>{row.price_strategy_sn}</a>
              ]
            }
          }
        },
        {
          title: '组织名称',
          field: 'company_name',
          width: 150
        },
        {
          title: '策略名称',
          field: 'price_strategy_name',
        },
        {
          title: '品牌',
          field: 'brand_name',
          width: 70
        },
        {
          title: '策略有效年月',
          field: 'effective_time',
          headerAlign: "right",
          align: "right",
          width: 170
        },
        {
          title: '审核状态',
          field: 'auditor_status_name',
          width: 70,
        },
        {
          title: '创建时间',
          field: 'add_time',
          width: 150
        },
        {
          title: '创建人',
          field: 'add_user_name',
          width: 140
        },
        {
          title: '审核时间',
          field: 'auditor_time',
          width: 160
        },
        {
          title: '审核人',
          field: 'auditor_name',
          width: 84
        },
      ]
    }
  },
  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "页小计";
          }
          if (["total_num", "total_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "total_price") {
              return this.$publicFution.priceFormatNone(num)
            }
            if (column.property === "total_num") {
              return this.$publicFution.priceFormatNone(num.toFixed(2))
            }
            return Number.isInteger(num) ? num : num.toFixed(2);
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "合计";
          }
          if (column.property === 'total_num' && this.footerData.total_num) {
            return this.$publicFution.priceFormatNone(this.footerData.total_num - 0)
          }
          if (column.property === 'total_price' && this.footerData.total_price) {
            return this.$publicFution.priceFormatNone((this.footerData.total_price - 0).toFixed(2))
          }
          if (["total_num", "total_price"].includes(column.property)) {
            return '计算中...'
          }
          return null;
        })]
    }
  },
}